// pages/PostUpdateForm.jsx

import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react';
import { Button, Container, FloatingLabel, Form } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';

function PostUpdateForm(props) {

    //   /posts/:id/edit    object 
    // {id: ' '}  object .    params.id .
    const params = useParams();

    const [post, setPost] = useState({});

    let savedPost = useRef(null); // savedPost  Object  current      .

    useEffect(()=>{
        //           
        axios.get(`/v3/posts/${params.id}`)
        .then(res=>{
            setPost(res.data);
            // useRef()  object  current   post .
            savedPost.current = res.data;
        })
        .catch(error=>console.log(error));
    },[])

    // title or author  change      .
    const handleChange = (e) => {
        setPost({
            ...post,
            [e.target.name]:e.target.value
        })
    };

    const navigate = useNavigate();

    const update = () => {
      axios.put(`/v3/posts/${post.id}`, post)
                .then(res=>{
                    alert(" !");
                    navigate("/posts");
                })
                .catch(error=>console.log(error));
    };
    const cancel = () => {
      setPost(savedPost.current);
    };

    return (
        <>
          <h1>{post.id}  </h1>
          <Container>
            <FloatingLabel label=" " className='mb-3' controlId='id'>
              <Form.Control type="text" name="id" value={post.id} readOnly />
            </FloatingLabel>
            <FloatingLabel label="" className='mb-3' controlId='title'>
              <Form.Control type="text" name="title" value={post.title} placeholder=' ...' onChange={handleChange} />
            </FloatingLabel>
            <FloatingLabel label="" className='mb-3' controlId='author'>
              <Form.Control type="text" name="author" value={post.author} placeholder=' ...'onChange={handleChange} />
            </FloatingLabel>
            <Button onClick={update} className='btn btn-success me-3'></Button>
            <Button onClick={cancel} className='btn btn-danger'></Button>
          </Container>
        </>
    );
}

export default PostUpdateForm;