// src/router/index.jsx

import { createBrowserRouter, createHashRouter } from "react-router-dom";
import Game from "../pages/Game";
import Home from "../pages/Home";
import Study from "../pages/Study";
import Post from "../pages/Post";
import App from "../App";
import PostForm from "../pages/PostForm";
import PostView from "../pages/PostView";
import PostUpdateForm from "../pages/PostUpdateForm";

//      .
const routes=[
    // spring boot       index.html  .
    //  Home        .
    {path:"index.html", element:<Home/>},
    {path:"/", element:<Home/>},
    {path:"/game", element:<Game/>},
    {path:"/study", element:<Study/>},
    {path:"/posts", element:<Post/>},
    {path:"/posts/new", element:<PostForm/>},
    {path:"/posts/:id/view", element:<PostView/>},
    {path:"/posts/:id/edit", element:<PostUpdateForm/>}
];

//export  router  
const router = createBrowserRouter([{
    path:"/",
    element:<App/>,
    children: routes.map((route)=>{
        return {
            index: route.path === "/", // path  "/"  index     
            path: route.path === "/" ? undefined : route.path, // path  "/"     
            element: route.element //    
        }
    })
}]);

export default router;