import React from "react";
import axios from "axios";

function UpdateForm({ item, onCancel, onSuccess }) {
    return (
        <div className="card p-4 shadow-sm">
            <h3 className="mb-4 text-center"> </h3>
            <form
                onSubmit={(e) => {
                    e.preventDefault();
                    const formData = new FormData(e.target);
                    const obj = Object.fromEntries(formData);
                    axios
                        .patch("/v3/posts/" + item.id, obj)
                        .then((res) => {
                            alert(" !");
                            onSuccess();
                        })
                        .catch((error) => console.log(error));
                }}
            >
                <div className="mb-3">
                    <label htmlFor="title" className="form-label">
                        
                    </label>
                    <input
                        type="text"
                        id="title"
                        name="title"
                        defaultValue={item.title}
                        className="form-control"
                        placeholder=" ."
                    />
                </div>
                <div className="mb-3">
                    <label htmlFor="author" className="form-label">
                        
                    </label>
                    <input
                        type="text"
                        id="author"
                        name="author"
                        defaultValue={item.author}
                        className="form-control"
                        placeholder=" ."
                    />
                </div>
                <div>
                    <button type="submit" className="btn btn-success me-3"> </button>
                    <button type="button" className="btn btn-danger" onClick={onCancel}></button>
                </div>
            </form>
        </div>
    );
}

export default UpdateForm;