// pages/PostForm.jsx

import axios from 'axios';
import React from 'react';
import { Button, FloatingLabel, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
//import "bootstrap/dist/css/bootstrap.min.css"; // Bootstrap 

function PostForm(props) {
    // javascript  page   hook 
    const navigate=useNavigate();

    return (
        <>
            <h1>  </h1>
            <Form action="/v3/posts" method="post" onSubmit={(e)=>{
                e.preventDefault();
                const url=e.target.action;
                //    FormData  . 
                const formData=new FormData(e.target);
                //   object  
                const obj=Object.fromEntries(formData);
                //axios   post  .
                axios.post(url, obj)
                .then(res=>{
                    //  .
                    console.log(res.data);
                    alert(res.data.id+"   .");
                    // "/posts"
                    navigate("/posts")
                })
                .catch(err=>console.log(err));
            }}>
                <FloatingLabel label="" className="mb-3" controlId="title">
                    <Form.Control type="text" name="title" placeholder=" ..."/>
                </FloatingLabel>
                <FloatingLabel label=""className="mb-3" controlId="author">
                    <Form.Control type="text" name="author" placeholder=" ..."/>
                </FloatingLabel>
                <Button type="submit" variant="success"></Button>
            </Form>
        </>
    );
}

export default PostForm;