import React from "react";
import axios from "axios";

function InsertForm({ onCancel, onSuccess }) {
    return (
        <div className="card p-4 shadow-sm">
            <h3 className="mb-4 text-center"> </h3>
            <form onSubmit={(e) => {
                e.preventDefault();
                const formData = new FormData(e.target);
                const obj = Object.fromEntries(formData);
                axios
                    .post("/v3/posts", obj)
                    .then(res => {
                        alert(" !");
                        onSuccess();
                    })
                    .catch(error => console.log(error));
            }}>
                <div className="mb-3">
                    <label htmlFor="title" className="form-label">
                        
                    </label>
                    <input
                        type="text"
                        id="title"
                        name="title"
                        className="form-control"
                        placeholder=" ."
                    />
                </div>
                <div className="mb-3">
                    <label htmlFor="author" className="form-label">
                        
                    </label>
                    <input
                        type="text"
                        id="author"
                        name="author"
                        className="form-control"
                        placeholder=" ."
                    />
                </div>
                <div>
                    <button type="submit" className="btn btn-success me-3"> </button>
                    <button type="button" className="btn btn-danger" onClick={onCancel}></button>
                </div>
            </form>
        </div>
    );
}

export default InsertForm;
