// src/pages/Post.jsx

import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { NavLink, useNavigate, useSearchParams } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import InsertForm from '../components/PostInsert';
import UpdateForm from '../components/PostUpdate';

function Post(props) {
    // "/post?pageNum=x"  pageNum   query parameter     hook
    const [params, setParams] = useSearchParams({ pageNum: 1 });
    // "/post?item.id=x"  item.id   query parameter     hook
    const [view, setView] = useSearchParams(0);

    const navigate = useNavigate();
    const move = (pageNum) => {
        navigate(`/posts?pageNum=${pageNum}`);
    };
    const viewpost = (num) => {
        navigate(`/posts/${num}`);
    };

    const [pageInfo, setPageInfo] = useState({
        list: [],
    });

    const [pageArray, setPageArray] = useState([]);

    function range(start, end) {
        const result = [];
        for (let i = start; i <= end; i++) {
            result.push(i);
        }
        return result;
    }

    const refresh = (pageNum) => {
        axios
            .get("/v3/posts?pageNum=" + pageNum)
            .then((res) => {
                setPageInfo(res.data);
                const result = range(res.data.startPageNum, res.data.endPageNum);
                setPageArray(result);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        // query    .
        let pageNum = params.get("pageNum");

        refresh(pageNum);
    }, [params]);

    const [editingPostId, setEditingPostId] = useState(null);
    const handleEditCheck = (id) => {
        setEditingPostId(id);
    };
    const handleCancelUpdateClick = () => {
        setEditingPostId(null);
    };
    const handleUpdateSuccess = () => {
        setEditingPostId(null); //     
        move(pageInfo.pageNum); //  
    };

    const [inserting, setInserting] = useState(null);
    const handleInsertCheck = () => {
        setInserting(1);
    };
    const handleCancelInsertClick = () => {
        setInserting(null);
    };
    const handleInsertSuccess = () => {
        setInserting(null);
        move(1);
    };

    return (
        <>
            <button className="btn btn-info" onClick={() => {
                handleInsertCheck();
            }}> </button>
            {inserting === 1 && (
                <>
                    <InsertForm onCancel={handleCancelInsertClick} onSuccess={handleInsertSuccess}/>
                </>
            )}
            <h1 className="text-center mb-4"> </h1>
            <NavLink to="/posts/new">  </NavLink>
            <table className="table table-striped table-hover table-bordered text-center">
                <thead className="table-dark">
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>()</th>
                        <th>()</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {pageInfo.list.length > 0 ? (
                        pageInfo.list.map((item) => (
                            <React.Fragment key={item.id}>
                                <tr>
                                    <td>{item.id}</td>
                                    <td><NavLink to={`/posts/${item.id}/view`}>{item.title}</NavLink></td>
                                    <td>{item.author}</td>
                                    <td><button className="btn btn-success" onClick={() => {
                                        handleEditCheck(item.id);
                                    }}>()</button></td>
                                    <td>
                                        <NavLink to={`/posts/${item.id}/edit`}>()</NavLink>
                                    </td>
                                    <td><button className="btn btn-danger" onClick={() => {
                                        axios.delete("/v3/posts/" + item.id)
                                            .then(res => {
                                                alert(res.data.id + "  .");
                                                refresh(pageInfo.pageNum);
                                            })
                                    }}></button></td>
                                </tr>
                                {editingPostId === item.id && (
                                    <tr>
                                        <td colSpan="6">
                                            <UpdateForm item={item} onCancel={handleCancelUpdateClick} onSuccess={handleUpdateSuccess} />
                                        </td>
                                    </tr>
                                )}
                            </React.Fragment>
                        ))
                    ) : (
                        <tr>
                            <td colSpan="5" className="text-center">
                                 .
                            </td>
                        </tr>
                    )}
                </tbody>
            </table>
            <nav>
                <ul className="pagination">
                    {pageInfo.startPageNum > 1 && (
                        <li className="page-item">
                            <button className="page-link" onClick={(e) => {
                                e.preventDefault();
                                //refresh(pageInfo.startPageNum - 1);
                                move(pageInfo.startPageNum - 1);
                            }}>
                                
                            </button>
                        </li>
                    )}
                    {pageArray.map(num => (
                        <li key={uuid()} className={`page-item ${pageInfo.pageNum === num ? 'active' : ''}`}>
                            <button
                                className="page-link"
                                onClick={(e) => {
                                    e.preventDefault();
                                    //refresh(num);
                                    move(num);
                                }}>
                                {num}
                            </button>
                        </li>
                    ))}
                    {pageInfo.endPageNum < pageInfo.totalPageCount && (
                        <li className="page-item">
                            <button className="page-link" onClick={(e) => {
                                e.preventDefault();
                                //refresh(pageInfo.endPageNum + 1);
                                move(pageInfo.endPageNum + 1);
                            }}>
                                
                            </button>
                        </li>
                    )}
                </ul>
            </nav>
        </>
    );
}


export default Post;